.gradeFormInput {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 0.5rem;
    padding-left: 0.5rem;
    background-color: rgb(243, 243, 243);
    border-radius: 5px;
}

.gradeFormInput .MuiTextField-root {
    margin-right: 16px; 
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}
  
.gradeFormInput .MuiTextField-root:last-child {
    margin-right: 0; /* Remove margin from the last TextField component */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}

.gradeBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.gradeBtnUpateCancel {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
    
}

.gradeBtnUpateCancel button {
    margin-left: 1rem;
}

.subjectMainContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0.5rem;
    padding-left: 0.5rem;
    background-color: rgb(243, 243, 243);
    border-radius: 5px;
    margin-top: 1rem;
    color: black;
}

.addUpdateSubject {
    display: flex;
    flex-direction: row;
}

.addUpdateSubject button {
    margin-left: 1rem;
    width: 100px;
}

.fieldSubjectListProperties {
    display: flex;
    flex-direction: row;
}

.fieldSubjectTimeListProperties {
    display: flex;
    flex-direction: column;
}

.fieldTimeListProperties {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    background-color: aqua;
}

/* Custom styles for TimePicker */
.time-picker {
    width: 200px; /* Adjust the width as needed */
    height: 40px; /* Adjust the height as needed */
    font-size: 16px; /* Adjust the font size as needed */
    /* Add any other custom styles here */
}
  
.react-time-picker__wrapper {
    font-size: 16px; /* Adjust the font size as needed */
  }
  
  .react-time-picker__inputGroup {
    width: 150px; /* Adjust the width as needed */
  }
  
  .react-time-picker__inputGroup__input {
    height: 40px; /* Adjust the height as needed */
  }

.fieldSubjectListProperties button {
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-left: 1rem;
}

.fieldExamListProperties {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0.5rem;
}

.examSubjectList {
    display: flex;
    flex-direction: column;
    background-color: brown;
    padding: 0.5rem;
    margin-left: 0.5rem;
    margin-bottom: 1rem;
    width: 400px;
    border-radius: 5px;
}

.examSubjectList input {
    padding: 0.5rem;
    margin-bottom: 0.5rem;
    border-radius: 5px;
    color: white;
}

.monthRemove {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 0.5rem;
}

.monthRemove input {
    color: black;
}

.monthRemove select {
    padding: 0.5rem;
    border-radius: 5px;
    
}

.monthRemove button {
    border: none;
    border-radius: 5px;
    margin-bottom: 0.5rem;
}

.tableMainChartContainer {
    background-color: #f9f9f9;
    color: black;
    margin-top: 1rem;
    border-radius: 5px;
    padding: 0.5rem;
}  

.dataTableContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0.5rem;
    background-color: aliceblue;
    color: black;
}

@media screen and (max-width: 768px) {
    .addUpdateSubject {
        display: flex;
        flex-direction: column;
    }

    .addUpdateSubject button {
        margin-bottom: 0.5rem;
    }
    

}
