.userContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.usertopbar {
    display: flex;
    flex-direction: column;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    padding-left: 2rem;
    padding-bottom: 1rem;
}

.userFormTopbar {
    display: flex;
    flex-direction: column;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    padding-left: 2rem;
    padding-bottom: 1rem;
    padding-top: 1rem;

}

.userFormTopbar button {
    width: 100px;
}

.usertopbar button {
    width: 200px;
}

.userMainBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    margin-top: 2rem;
    padding: 0.5rem;
  
}

.userFormMainBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(236, 236, 236);
    border-radius: 5px;
    margin-top: 2rem;
    padding: 0.5rem;

}

.userFormInput {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 0.5rem;
    padding-left: 0.5rem;

}

.userFormInput .MuiTextField-root {
    margin-right: 16px; 
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}
  
.userFormInput .MuiTextField-root:last-child {
    margin-right: 0; /* Remove margin from the last TextField component */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}

@keyframes zoomInOut {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
}

.loadingUser {
    display: flex;
    justify-content: center;
    align-items: center;
    animation: zoomInOut 2s infinite;
    margin-top: 5rem;

}

@media screen and (max-width: 1200px) {
  
}
  
@media screen and (max-width: 768px) {
    .usertopbar {
        justify-content: center;
        align-items: center;
    }
    

}