.dashboardContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.dashboardTopbar {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: aliceblue;
  border-radius: 5px;
  height: 6rem;
}

.mainSearchInputForm {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 0.5rem;
  width: 50%;
  gap: 1rem;
}

.mainSearchStudentInputForm {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  gap: 1rem;
  margin-top: 0.9rem;
  width: 100%;
}

.studentSearchContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: aliceblue;
  color: black;
  margin-top: 1rem;
  border-radius: 5px;
  padding: 0.5rem;
}

.selectInbox {
  width: 50%;
}

.studentFinanceDataSelect {
    display: flex;
    flex-direction: row;
    width: 100%;
    gap: 1rem;
}

.studentFinanceDataList {
    display: flex;
    flex-direction: column;
    width: 100%;
}