.setHeaderColumn {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh; /* Ensure container takes up full viewport height */
}

.setHeaderContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap; /* Prevent wrapping of items to ensure horizontal scrolling */
    overflow-x: auto; /* Enable horizontal scrolling */
    align-items: center;
    background-color: rgba(69, 68, 68, 0.632);
    height: 280px;
    border-radius: 5px;
    padding: 1rem;
}


.setHeaderContainer .card {
    flex: 0 0 auto; /* Prevent items from growing or shrinking */
    margin: 0 30px; /* Adjust spacing between items */
}


.setMainContainer {
    display: flex;
    flex-direction: column;
    background-color: rgba(69, 68, 68, 0.632);
    flex-grow: 1; /* Allow the mainBarLayout to take up remaining space */
    width: 100%;
    margin-top: 2rem;
    padding: 0.5rem;
    overflow-y: auto; /* Apply overflow to mainBarLayout only */
    max-height: calc(100vh - 200px - 2rem); /* Set max height to viewport height minus top bar height and margin */
}



.cardRow {
    display: flex;
    flex-direction: row;
}

.card {
    width: 150px;
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    transition: transform 0.3s; /* Add transition for smooth animation */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
    transition: transform 0.3s, background-color 0.3s;
}
  
.card:hover {
    transform: scale(1.1); /* Zoom in card on hover */
    color: white;
    background-color: rgba(2, 92, 165, 0.3);
}
  
.card-image {
    width: 100%;
    height: 150px;
    border-radius: 8px;
    object-fit: cover;
    transition: transform 0.3s; /* Add transition for smooth animation */
}
  
.card:hover .card-image {
    transform: scale(0.8); /* Zoom out image on hover */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card:hover .card-title {
    color: white; /* Change color of card title on hover */
}
  
.card-content {
    padding: 16px;
}
  
.card-title {
    margin-top: 0;
    font-size: 1rem;
    color: black;
}

.LayoutContainer {
    display: flex;
    height: 100vh;
    overflow: auto; /* Apply overflow to the entire LayoutContainer */
}








