.teacherContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.teachertopbar {
    display: flex;
    flex-direction: column;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    padding-left: 2rem;
    padding-bottom: 1rem;
}

.teacherFormTopbar {
    display: flex;
    flex-direction: column;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    padding-left: 2rem;
    padding-bottom: 1rem;
    padding-top: 1rem;

}

.teacherFormTopbar button {
    width: 100px;
}

.teachertopbar button {
    width: 200px;
}

.teacherMainBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    margin-top: 2rem;
    padding: 0.5rem;
  
}

.teacherFormMainBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(236, 236, 236);
    border-radius: 5px;
    margin-top: 2rem;
    padding: 0.5rem;
    color: black;

}

.teacherFormInput {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 0.5rem;
    padding-left: 0.5rem;

}

.teacherFormInput .MuiTextField-root {
    margin-right: 16px; 
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}
  
.teacherFormInput .MuiTextField-root:last-child {
    margin-right: 0; /* Remove margin from the last TextField component */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}

.mainUploadContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}

.uploadPhoto {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border: 2px solid #ccc;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    overflow: hidden; /* Ensure the image doesn't overflow the container */
  }
  
  
.uploadPhoto:hover {
    background-color: #f0f0f0;
}

.teacherBtnContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}

.teacherCardContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0.5rem;
  }

  
  .teacherCard {
    display: flex;
    flex-direction: row;
    flex: 0 0 calc(33.33% - 1rem); /* Adjust width and margin as per your preference */
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 16px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease; /* Add transition for smooth effect */
    cursor: pointer;
  }
  
  .teacherCard img {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .teacherCard:hover {
    transform: scale(0.8); /* Scale down the card slightly on hover */
  }
  
  .teacherCardImg {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(2, 92, 165, 0.3);
    padding: 0.5rem;
    border-radius: 5px;
  }
  
  .teacherCardName {
    padding: 0.5rem;
  }
  
  .teacherCardName p {
    font-family: "Trirong", serif;
    text-shadow: 3px 3px 3px #ababab;
  }
  
  .teacherCard img {
    width: 100px; /* Adjust according to your preference */
    height: 100px; /* Adjust according to your preference */
    border-radius: 50%; /* To make the image circular */
    object-fit: cover; /* Ensure the image fills the circle */
    margin-bottom: 8px;
  }
  
  .teacherCard h3 {
    margin: 0;
    font-size: 18px;
    color: #333;
  }

  @keyframes zoomInOut {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
}

.loadingTeacher {
    display: flex;
    justify-content: center;
    align-items: center;
    animation: zoomInOut 2s infinite;
    margin-top: 5rem;

}

.loadingContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 5rem;

}

.loadingContainer p {
  margin-top: 2rem;
}

@media screen and (max-width: 1200px) {
  
}
  
@media screen and (max-width: 768px) {
    .teachertopbar {
        justify-content: center;
        align-items: center;
    }

    .teacherCard {
        flex-direction: column;
        flex: 0 0 calc(100% - 1rem);/* Adjust width for two cards per row */
    }
    

}