.subjectMainNestContainer {
    display: flex;
    flex-direction: column;
    background-color: aliceblue;
    margin-top: 1rem;
    padding: 0.5rem;
    border-radius: 5px;
}

.subTimeBtn {
    display: flex;
    flex-direction: row;
}

.subTimeBtn button {
    margin-left: 0.5rem;
}

.timeSubTeacherContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: aliceblue;
    margin-top: 1rem;
    padding: 0.5rem;
    color: black;
    border-radius: 5px;
}

.timeSubTeacherBtn {
    display: flex;
}

.timeSubTeacherBtn button {
    margin-left: 0.5rem;
}

.fieldTeacherAddListProperties {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    margin-bottom: 0.5rem;
}

.fieldTeacherAddListProperties button {
    height: 45px;
}

.addTeacherBtn {
    display: flex;
    justify-content: center;
    align-items: center;
}