.addTeachMainContainer {
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    margin-top: 1rem;
    border-radius: 5px;
    color: black;
    padding: 0.5rem;
}

.addTeachGradeContainer {
    display: flex;
    flex-direction: column;
}

.addTeachExistNewContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between; /* Adjusted to space-between */
    align-items: flex-start; /* Adjusted to flex-start */
    margin-top: 1rem;
}

.removeTeachExistNewContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
   
}

.existStudents {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* Adjusted to flex-start */
    justify-content: flex-start; /* Adjusted to flex-start */
    background-color: blue;
    width: 48%; /* Adjusted to leave space for margins */
    padding: 0.5rem;
  }

  .existRemoveStudents {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
  }
  
  .newStudents {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* Adjusted to flex-start */
    justify-content: flex-start; /* Adjusted to flex-start */
    background-color: brown;
    width: 48%; /* Adjusted to leave space for margins */
    padding: 0.5rem;
  }

  .teacherSubMainContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: aliceblue;
    color: black;
    padding: 0.5rem;
  }

  .teacherSubBtn {
    display: flex;
  }

  .teacherSubBtn button {
    margin-left: 1rem;
  }

  .existAdd {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0.5rem;
  }

