.academicMainContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.academicInputForm {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: aliceblue;
    height: 100px;
    border-radius: 5px;
    padding: 0.5rem;
}

.academicFormList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: aliceblue;
    border-radius: 5px;
    padding: 0.5rem;
    color: black;
    margin-top: 2rem;

}

.academicHeaderBtn {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: aliceblue;
    height: 100px;
    border-radius: 5px;
    padding: 0.5rem;
    
}

.academicStudentContainer {
    display: flex;
    flex-direction: column;
    background-color: aliceblue;
    color: black;
    border-radius: 5px;
    width: 100%;
}

.academicStudentGradeSelect {
    padding: 0.5rem;
}

.academicStudentUpdateBtn {
    display: flex;
    padding: 0.5rem;

}

.RemoveDetails {
    color: #ff0000 !important;
}

@media screen and (max-width: 1200px) {
  
}
  
@media screen and (max-width: 768px) {
    .academicInputForm {
        flex-direction: column;
    }

    .academicInputForm input {
        width: 100%;
    }

    .academicInputForm button {
        margin-top: 10px;
    }
    
    

}






