.studentContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.studenttopbar {
    display: flex;
    flex-direction: column;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    padding-left: 2rem;
    padding-bottom: 1rem;
}

.studentFormTopbar {
    display: flex;
    flex-direction: column;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    padding-left: 2rem;
    padding-bottom: 1rem;
    padding-top: 1rem;

}

.studentFormTopbar button {
    width: 100px;
}

.studenttopbar button {
    width: 200px;
}

.studentMainBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(69, 68, 68, 0.632);
    border-radius: 5px;
    margin-top: 2rem;
    padding: 0.5rem;
    
  
}

.studentFormMainBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(236, 236, 236);
    border-radius: 5px;
    margin-top: 2rem;
    padding: 0.5rem;
    color: black;

}

.studentFormInput {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 0.5rem;
    padding-left: 0.5rem;

}

.studentFormInput .MuiTextField-root {
    margin-right: 16px; 
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}
  
.studentFormInput .MuiTextField-root:last-child {
    margin-right: 0; /* Remove margin from the last TextField component */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}

.sortGradeStudent {
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.studentBtnContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}

.studentTableContainer {
    display: flex;
    margin-top: 2rem;
    flex-direction: column;
}

.studentGradeSelect {
    width: 100%;
    padding: 1rem;
}

.studentPrintBtn {
    display: flex;
    margin-bottom: 1rem;
}

.totalStudentLength {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}



.Table td,
th {
  border: none !important;
}

td {
    cursor: pointer;
}

.tableHeadTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.tableName {
  display: flex;
  flex-direction: column;
  background-color: aqua;
}

.Table:first-child {
  border-radius: 0.7rem !important;
}
.status {
  padding: 8px;
  border-radius: 9px;
}

.Details {
  color: #00b5ff !important;
}

@keyframes zoomInOut {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
}

.loadingStudent {
    display: flex;
    justify-content: center;
    align-items: center;
    animation: zoomInOut 2s infinite;
    margin-top: 5rem;

}

.studentNavigateBtn {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.studentNavigateBtn button {
    margin-left: 1rem;
}

.studentGradeMainContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.sortGradeStudent {
    background-color: aliceblue;
    padding: 0.5rem;
    border-radius: 5px;
    margin-bottom: 0.5rem;
}

.studentPrintBtn {
    margin-bottom: 0.5rem;
}

.sortGradeRoomStudent {
    background-color: aliceblue;
    padding: 0.5rem;
    border-radius: 5px;
    margin-bottom: 0.5rem;
    color: black;

}

.studentSearchForm {
    display: flex;
}

.studentSearchForm input {
    padding: 0.5rem;
    border: 1px solid white;
    border-radius: 5px;
    width: 250px;
}

.studentSearchForm button {
    border-radius: 5px;
}





@media screen and (max-width: 1200px) {
  
}
  
@media screen and (max-width: 768px) {
    .studenttopbar {
        justify-content: center;
        align-items: center;
    }

    .studentNavigateBtn {
        flex-direction: column;
    }

    .studentNavigateBtn button {
        margin-bottom: 1rem;
    }
    

}